.Input-text:-ms-input-placeholder + .Input-label {
  visibility: hidden;
  z-index: -1;
}

.Input-text:placeholder-shown + .Input-label {
  visibility: hidden;
  z-index: -1;
}

.Input-text:not(:-ms-input-placeholder) + .Input-label, .Input-text:focus:not(:-ms-input-placeholder) + .Input-label {
  visibility: visible;
  z-index: 1;
  opacity: 1;
  transform: translate3d(0, calc((50%) - 18px), 0)
    scale(0.8);
  transform: translate3d(0, var(--labelTransformedPosY), 0)
    scale(var(--labelScaleFactor));
  -ms-transition: transform 300ms, visibility 0ms,
    z-index 0ms;
  transition: transform 300ms, visibility 0ms,
    z-index 0ms;
  -ms-transition: transform var(--inputTransitionDuration), visibility 0ms,
    z-index 0ms;
  transition: transform var(--inputTransitionDuration), visibility 0ms,
    z-index 0ms;
}

.Input-text:not(:placeholder-shown) + .Input-label,
.Input-text:focus:not(:placeholder-shown) + .Input-label {
  visibility: visible;
  z-index: 1;
  opacity: 1;
  -webkit-transform: translate3d(0, calc((50%) - 18px), 0)
    scale(0.8);
          transform: translate3d(0, calc((50%) - 18px), 0)
    scale(0.8);
  -webkit-transform: translate3d(0, var(--labelTransformedPosY), 0)
    scale(var(--labelScaleFactor));
          transform: translate3d(0, var(--labelTransformedPosY), 0)
    scale(var(--labelScaleFactor));
  transition: visibility 0ms,
    z-index 0ms, -webkit-transform 300ms;
  transition: transform 300ms, visibility 0ms,
    z-index 0ms;
  transition: transform 300ms, visibility 0ms,
    z-index 0ms, -webkit-transform 300ms;
  transition: visibility 0ms,
    z-index 0ms, -webkit-transform var(--inputTransitionDuration);
  transition: transform var(--inputTransitionDuration), visibility 0ms,
    z-index 0ms;
  transition: transform var(--inputTransitionDuration), visibility 0ms,
    z-index 0ms, -webkit-transform var(--inputTransitionDuration);
}

